/* Basic styles for the entire app */


/* Box around h1 */
.box {
    text-align: center;
    border: 1px solid #000;
    padding: 20px;
    margin: 20px auto 10px;
    max-width: 80%;
    font-family: 'Roboto', sans-serif;
  }
  
  /* Style for h1 */
  .box h1 {
    margin: 0;
    font-size: 35px; /* Adjust font size as needed */
    font-weight: 700;
    text-shadow: 2px 2px 4px rgba(22, 22, 21, 0.499);
  }
  
  
  
  
  .App {
      text-align: center;
      font-family: Arial, sans-serif;
      margin-top: 20px;
    }
    
    /* Container for horizontal artworks */
    .art-container-horizontal {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; /* Distribute space between items */
      max-width: 800px; /* Adjust as needed */
      margin: 0 auto; /* Center the container */
    }
    
    /* Individual art piece */
    .art-piece-horizontal {
      width: calc(25% - 20px); /* 25% width for 4 items in a row, considering margins */
      margin: 10px; /* Adjust margin between items */
      text-align: center;
      box-sizing: border-box; /* Include padding and border in width */
    }
    
    .art-piece-horizontal img {
      width: 100%;
      height: auto;
      border: 1px solid #ccc;
    }
    
    .art-piece-horizontal p {
      margin-top: 5px;
      font-size: 14px;
      color: #333;
    }
    